import cartcontrol from '@/components/cartcontrol.vue'
// import {mul} from '@/utils/lib'
export default {
	props: {
		goods: {
			type: Array
		}
	},
	data() {
		return {
			isShowList: false,
		};
	},
	components: {
		cartcontrol
	},
	computed: {
		getList() {
			let result = [];
			this.goods.forEach((good) => {
				good.foods.forEach((food) => {
					if (food.count) {
						result.push(food)
						// console.log('res', JSON.stringify(result))
					}
				})
			})
			return result
		},
		// 获得购物车所有商品数量
		getAllCount() {
			let result = 0;
			this.getList.forEach((food) => {
				result += food.count
			})
			return result
		},



		// 总价
		getAllPrice() {
			let result = 0;
			let result1 = 0;
			this.goods.forEach((good) => {
				good.foods.forEach((food) => {
					result1 += this.accMul(food.count, food.price)
					result = result1.toFixed(2);
				})
			})
			return result
		}
	},
	methods: {
		// 解决浮点数 运算出现多位小数
		accMul(arg1, arg2) {
			let m = 0,
				s1 = '',
				s2 = '';
			if (arg1 && arg1 != null)
				s1 = arg1.toString();
			if (arg2 && arg2 != null)
				s2 = arg2.toString();
			// console.log('m1',s2.replace('.',''))
			try {
				m += s1.split('.')[1].length
			} catch (e) {}
			try {
				m += s2.split('.')[1].length
			} catch (e) {}

			return Number(s1.replace('.', '')) * Number(s2.replace('.', '')) / Math.pow(10, m);
		},
		showCart(){
			this.isShowList = !this.isShowList;
		},
		toggleList(){ // 提交订单
			let arr = []
			this.getList.forEach(function(item){
				arr.push({foodId: item.id,num:item.count})
			})
			let strData = JSON.stringify(arr)
			// console.log(strData)
			if (this.getList.length) {
				// this.isShowList = !this.isShowList;
				uni.request({
					url: this.nowUrl + '/foods/shopcar/build',
					header:{
						'token': this.token,
						'content-type': 'application/json'
					},
					method:'POST',
					data: strData,
					success(res) {
						// console.log(res.data.data)
						let dataInfo = JSON.stringify(res.data.data)
						if(res.data.code == 200){
							uni.navigateTo({
								url: '../orderDetails/orderDetails?dataInfo=' + dataInfo,
								animationDuration: 200,
								animationType: "slide-in-right"
							})
						}else{
							uni.showToast({
								title: res.data.msg,
								duration:1000,
								icon:'none'
							})
							return false
						}
					}
				})
			}else{
				uni.showToast({
					title: '您的购物车空的',
					duration:1000,
					icon: 'none'
				})
			}
		},
		delShopcart() {
			this.$emit('delAll');
		},
		addCart: function(item) {
			this.$emit('add', item)
		},
		decreaseCart(item) {
			this.$emit('dec', item)
		}

	},
}
